.autocomplete {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 8px;
  background: #fff;
  &__label {
    padding-right: 8px;
  }
  &__input {
    margin-right: 8px;
  }
  p {
    margin-bottom: 0;
  }
}

[id="distance-select"] [type="radio"] {
  display: none;
}
input[type="radio"]:checked + label {
  background: #2b3b48;
  color: #f6fafd;
}
[id="distance-select"] label {
  cursor: pointer;
  padding: 8px;
  width: 84px;
  display: inline-block;
  text-align: center;
  border: 2px solid #2b3b48;
  margin: 4px;
  border-radius: 18px;
}



